import { useEffect } from "react";
import { infoApi } from "../../hook/api";
import { useRequest } from "../useRequest";
import { useAlert } from "../";

const useProfileInfo = (setInfo, userId) => {
  // info  get  api
  const { getInfo } = infoApi();
  const { alertState, alertAction } = useAlert();

  // info get     
  const [
    { data: response, fulfilled, pending, rejected, error },
    { run: getInfoApi },
  ] = useRequest(getInfo);

  //    
  useEffect(() => {
    getInfoApi(userId);
  }, [userId]);

  // info get 
  useEffect(() => {
    if (fulfilled) {
      setInfo({
        userName: response.userName,
        role: response.role,
        stacks: response.stacks,
        contact: response.contact,
        area: response.area,
        grade: response.grade,
        introduction: response.introduction,
      });
    }
  }, [fulfilled]);

  // info get 
  useEffect(() => {
    if (rejected) {
      if (error) {
        alertAction.open(error.response.data.message);
        console.log(error);
      }
    }
  }, [rejected]);

  // get refresh~
  const Refresh = () => {
    Api();
  };

  return pending;
};

export default useProfileInfo;
